.App {
  text-align: center;
  display: flex;
  justify-content: space-evenly;
}

body {
  background-color: whitesmoke;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.location-data {
  margin: 2rem;
}

.blocks-container {
  display: flex;
  flex-direction: row;  
}

.start-button {
    width: 20vw ;
    height: auto;
    margin: 2rem;
    border: none;
    border-radius: 1rem;
    box-shadow:  inset 0 0 1rem 0 lightgray, 0.2rem 1rem 1rem 0.5rem lightgray;
    padding: 2rem;
    background-color: whitesmoke;
    transition: box-shadow 0.3s ease, background-color 0.3s ease;
    font-size: 1rem;
    font-weight: bold;
    color: #555;
    
}
.start-button:hover{
        
        box-shadow: 0 0.5rem 0.7rem 0.5rem lightgray inset, 0 0 0.5rem 0.2rem lightgray;
        background-color: rgb(235, 232, 232);
        
    }